html, body { margin: 0; }

#intro2 {
  height:(100vh - 4rem);
  width: 90rem;
  margin: 0;
  padding-bottom: 4rem;
  overflow: hidden;
  position: relative;

}


.bg {
  position: absolute;
  z-index: -10;
  top: -2rem;
  right: 2rem;
  object-fit: cover;
  height: 100vh;
  -webkit-animation-delay: 4s;
          animation-delay: 4s; 
  -webkit-animation: fadeInFromNone 12s ease-out;
    animation: fadeInFromNone 12s ease-out;
}



@-webkit-keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}

@keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}


