#cache {
    height:(100vh - 4rem);
    width: 100vw;
    margin: 0;
    overflow: hidden;
    position: relative;
    justify-content: center;
    z-index: -3;

}

.cacheBg {
    position: absolute;
    z-index: -30;
    top: -2rem;
    right: -2.5rem;
    object-fit: cover;
    height: 100vh;
    /* animation: blur 1.5s ease 0s ;
    animation-delay: 1s; 
    -webkit-animation: blur 1.5s ease 0s ;
    -moz-animation: blur 1.5s ease 0s ; */
    -webkit-animation-delay: 4s;
            animation-delay: 4s; 
    -webkit-animation: fadeInFromNone 4s ease-out;
      animation: fadeInFromNone 4s ease-out;
}


@-webkit-keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}

@keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}

/* @keyframes blur {
    0%,
    90% {
      -webkit-filter: blur(0px);
      -moz-filter: blur(0px);
      -o-filter: blur(0px);
      -ms-filter: blur(0px);
    }
    50% {
      -webkit-filter: blur(50px);
      -moz-filter: blur(50px);
      -o-filter: blur(50px);
      -ms-filter: blur(50px);
    }
  } */
  
.cacheContent {
    height: 100vh;
    width: 82rem;
    /* padding-right: 4rem; */
    padding-left: 8rem;
    padding-top:15rem;
    font-size: 3rem;
    font-weight: 700;
    margin:0;
    /* display: flex;
    flex-direction: column; */
    color: #061736;
    opacity: 0;
    -webkit-animation: slideRight 2s ease forwards;
            animation: slideRight 2s ease forwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s; 
}

@-webkit-keyframes slideRight {
    0%{
      -webkit-transform: translateX(-100px);
              transform: translateX(-100px);
      opacity: 0;
    }

    100% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        opacity: 1;
    }
  }

@keyframes slideRight {
    0%{
      -webkit-transform: translateX(-100px);
              transform: translateX(-100px);
      opacity: 0;
    }

    100% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
